import React, { useState } from "react";
import { useAppContext } from "./AppContext";
import { BASE_URL } from "../../config";
const CustomerModal = ({ isOpen, onClose }) => {
  const { showMessage, setLoading } = useAppContext(); // ‚úÖ AppContext-–æ–æ—Å –∞–≤–Ω–∞
  const [name, setName] = useState("");
  const [status, setStatus] = useState("–ò–¥—ç–≤—Ö–∏—Ç—ç–π");

  const handleCreate = async () => {
    if (!name.trim()) {
      showMessage("‚ö†Ô∏è –ù—ç—Ä–∏–π–≥ –æ—Ä—É—É–ª–Ω–∞ —É—É");
      return;
    }

    try {
      setLoading(true);

      const res = await fetch(`${BASE_URL}/api/customer`);
      const allCustomers = await res.json();

      const duplicate = allCustomers.find(
        (cus) => cus["name"].toLowerCase() === name.toLowerCase()
      );

      if (duplicate) {
        showMessage("‚ö†Ô∏è –ò–∂–∏–ª –Ω—ç—Ä—Ç—ç–π —Ö–∞—Ä–∏–ª—Ü–∞–≥—á –∞–ª—å —Ö—ç–¥–∏–π–Ω “Ø“Ø—Å—Å—ç–Ω –±–∞–π–Ω–∞");
        return;
      }

      const saveRes = await fetch(`${BASE_URL}/api/customer`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name,
          status,
        }),
      });

      const result = await saveRes.json();

      if (!saveRes.ok) {
        throw new Error(result.message || "–•–∞–¥–≥–∞–ª–∞—Ö “Ø–µ–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞");
      }

      showMessage("‚úÖ –•—ç—Ä—ç–≥–ª—ç–≥—á –∞–º–∂–∏–ª—Ç—Ç–∞–π –Ω—ç–º—ç–≥–¥–ª—ç—ç");
      setName("");
      setStatus("–ò–¥—ç–≤—Ö–∏—Ç—ç–π");
    } catch (err) {
      console.error("–•—ç—Ä—ç–≥–ª—ç–≥—á –Ω—ç–º—ç—Ö—ç–¥ –∞–ª–¥–∞–∞:", err);
      showMessage("‚ùå " + (err.message || "–°–µ—Ä–≤–µ—Ä—Ç—ç–π —Ö–æ–ª–±–æ–≥–¥–æ—Ö–æ–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞"));
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    setName("");
    setStatus("–ò–¥—ç–≤—Ö–∏—Ç—ç–π");
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div style={styles.overlay}>
      <div style={styles.modal}>
        <h2 style={styles.title}>–•—ç—Ä—ç–≥–ª—ç–≥—á –Ω—ç–º—ç—Ö</h2>

        <div style={styles.row}>
          <label>–ù—ç—Ä</label>
          <input
            type="text"
            value={name}
            placeholder="–•—ç—Ä—ç–≥–ª—ç–≥—á–∏–π–Ω –Ω—ç—Ä –æ—Ä—É—É–ª–Ω–∞ —É—É"
            onChange={(e) => setName(e.target.value)}
            style={styles.input}
          />
        </div>

        <div style={styles.row}>
          <label>–¢”©–ª”©–≤</label>
          <select
            value={status}
            onChange={(e) => setStatus(e.target.value)}
            style={styles.input}
          >
            <option value="–ò–¥—ç–≤—Ö–∏—Ç—ç–π">–ò–¥—ç–≤—Ö–∏—Ç—ç–π</option>
            <option value="–ò–¥—ç–≤—Ö–≥“Ø–π">–ò–¥—ç–≤—Ö–≥“Ø–π</option>
          </select>
        </div>

        <div style={styles.buttonRow}>
          <button style={styles.submitButton} onClick={handleCreate}>
            –•–∞–¥–≥–∞–ª–∞—Ö
          </button>
          <button style={styles.cancelButton} onClick={handleClose}>
            –ë–æ–ª–∏—Ö
          </button>
        </div>
      </div>
    </div>
  );
};

const styles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    width: "100vw",
    height: "100vh",
    background: "rgba(0, 0, 0, 0.3)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    zIndex: 1000,
  },
  modal: {
    width: "400px",
    background: "#fff",
    padding: "20px",
    borderRadius: "5px",
    boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
    display: "flex",
    flexDirection: "column",
  },
  title: {
    fontSize: "18px",
    marginBottom: "15px",
    borderBottom: "1px solid #ddd",
    paddingBottom: "10px",
  },
  row: {
    display: "flex",
    flexDirection: "column",
    marginBottom: "10px",
  },
  input: {
    padding: "8px",
    fontSize: "14px",
    border: "1px solid #ccc",
    borderRadius: "4px",
    width: "100%",
    boxSizing: "border-box",
  },
  buttonRow: {
    display: "flex",
    justifyContent: "flex-end",
    gap: "10px",
    marginTop: "15px",
  },
  submitButton: {
    background: "#7367f0",
    color: "#fff",
    border: "none",
    padding: "10px 15px",
    cursor: "pointer",
    borderRadius: "4px",
  },
  cancelButton: {
    background: "#ccc",
    border: "none",
    padding: "10px 15px",
    cursor: "pointer",
    borderRadius: "4px",
  },
};

export default CustomerModal;
