import React, { useState, useEffect } from "react";
import { useAppContext } from "./AppContext";
import { BASE_URL } from "../../config";


const CreateAccount = ({ isOpen, onClose }) => {
  const { showMessage, setLoading } = useAppContext(); // ‚úÖ AppContext –∞—à–∏–≥–ª–∞–Ω–∞
  const [edd, setEdd] = useState("");
  const [dansniiNer, setDansniiNer] = useState("");
  const [currency, setCurrency] = useState("");
  const [salbar, setSalbar] = useState("");
  const [dansniiDugaar, setDansniiDugaar] = useState("");

  const [currencies, setCurrencies] = useState([]);
  const [branches, setBranches] = useState([]);
  const [glAccounts, setGlAccounts] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [currencyRes, branchRes, glRes] = await Promise.all([
          fetch(`${BASE_URL}/api/currency`),
          fetch(`${BASE_URL}/api/branch`),
          fetch(`${BASE_URL}/api/glaccount`),
        ]);

        setCurrencies(await currencyRes.json());
        setBranches(await branchRes.json());
        setGlAccounts(await glRes.json());
      } catch (error) {
        console.error("”®–≥”©–≥–¥”©–ª —Ç–∞—Ç–∞—Ö–∞–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞:", error);
        showMessage("‚ùå ”®–≥”©–≥–¥”©–ª —Ç–∞—Ç–∞—Ö–∞–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞");
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    if (edd && salbar) {
      generateAccountNumber();
    }

    if (edd) {
      const selectedAccount = glAccounts.find(
        (account) => account["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"] === edd
      );

      if (selectedAccount) {
        setDansniiNer(selectedAccount["–î–∞–Ω—Å–Ω—ã –Ω—ç—Ä"]);
        setCurrency(selectedAccount["–í–∞–ª—é—Ç"]);
      }
    } else {
      setDansniiNer("");
      setCurrency("");
    }
  }, [edd, salbar, glAccounts]);

  const padNumber = (number, length) => {
    return number.toString().padStart(length, "0");
  };

  const generateAccountNumber = () => {
    const selectedAccount = glAccounts.find(
      (account) => account["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"] === edd
    );

    if (selectedAccount && salbar) {
      const eddDugaar = selectedAccount["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"];
      const salbarCode = salbar;
      const tooluur = parseInt(selectedAccount["–¢–æ–æ–ª—É—É—Ä"]) + 1;

      const newAccountNumber = `${eddDugaar}${salbarCode}${padNumber(tooluur, 4)}`;

      setDansniiDugaar(newAccountNumber);
    }
  };

  const handleCreate = async () => {
    if (!dansniiDugaar || !dansniiNer || !currency || !salbar || !edd) {
      showMessage("‚ö†Ô∏è –ë“Ø—Ö —Ç–∞–ª–±–∞—Ä—ã–≥ –±”©–≥–ª”©–Ω”© “Ø“Ø");
      return;
    }

    try {
      setLoading(true);

      const res = await fetch(`${BASE_URL}/api/account`);
      const allAccounts = await res.json();
      const duplicate = allAccounts.find((acc) => acc["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"] === dansniiDugaar);

      if (duplicate) {
        showMessage("‚ö†Ô∏è –ò–∂–∏–ª –¥–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä –∞–ª—å —Ö—ç–¥–∏–π–Ω “Ø“Ø—Å—Å—ç–Ω –±–∞–π–Ω–∞");
        return;
      }

      const newAccount = {
        id: (allAccounts.length + 1).toString(),
        "–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä": dansniiDugaar,
        "–î–∞–Ω—Å–Ω—ã –Ω—ç—Ä": dansniiNer,
        "–í–∞–ª—é—Ç": currency,
        "–°–∞–ª–±–∞—Ä": branches.find((b) => b.code === salbar)?.name || salbar,
        "–ù—ç—ç—Å—ç–Ω –æ–≥–Ω–æ–æ": new Date().toLocaleString("en-GB"),
      };

      const saveRes =  await fetch(`${BASE_URL}/api/account`,{
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(newAccount),
      });

      const saveResult = await saveRes.json();

      if (!saveRes.ok) {
        throw new Error(saveResult.message || "–•–∞–¥–≥–∞–ª–∞—Ö “Ø–µ–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞");
      }

      const counterRes = await fetch(`${BASE_URL}/api/gl-tooluurchange`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ edd }),
      });

      const counterResult = await counterRes.json();

      if (!counterRes.ok) {
        throw new Error(counterResult.message || "–¢–æ–æ–ª—É—É—Ä —à–∏–Ω—ç—á–ª—ç—Ö—ç–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞");
      }

      showMessage("‚úÖ –î–∞–Ω—Å –∞–º–∂–∏–ª—Ç—Ç–∞–π “Ø“Ø—Å–ª—ç—ç");
      setEdd("");
      setDansniiNer("");
      setCurrency("");
      setSalbar("");
      setDansniiDugaar("");
    } catch (err) {
      console.error("–î–∞–Ω—Å “Ø“Ø—Å–≥—ç—Ö—ç–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞:", err);
      showMessage("‚ùå " + (err.message || "–°–µ—Ä–≤–µ—Ä—Ç—ç–π —Ö–æ–ª–±–æ–≥–¥–æ—Ö–æ–¥ –∞–ª–¥–∞–∞ –≥–∞—Ä–ª–∞–∞"));
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    setEdd("");
    setDansniiNer("");
    setCurrency("");
    setSalbar("");
    setDansniiDugaar("");
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div style={styles.overlay}>
      <div style={styles.modal}>
        <h2 style={styles.title}>–î–∞–Ω—Å “Ø“Ø—Å–≥—ç—Ö</h2>

        <div style={styles.row}>
          <label>–ï–î–î</label>
          <select
            value={edd}
            onChange={(e) => setEdd(e.target.value)}
            style={styles.input}
          >
            <option value="">–ï–î–î —Å–æ–Ω–≥–æ—Ö</option>
            {glAccounts.map((account, index) => (
              <option key={index} value={account["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"]}>
                {account["–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä"]} - {account["–î–∞–Ω—Å–Ω—ã –Ω—ç—Ä"]}
              </option>
            ))}
          </select>
        </div>

        <div style={styles.row}>
          <label>–°–∞–ª–±–∞—Ä</label>
          <select
            value={salbar}
            onChange={(e) => setSalbar(e.target.value)}
            style={styles.input}
          >
            <option value="">–°–∞–ª–±–∞—Ä —Å–æ–Ω–≥–æ—Ö</option>
            {branches.map((branch, index) => (
              <option key={index} value={branch.code}>
                {branch.name}
              </option>
            ))}
          </select>
        </div>

        <div style={styles.row}>
          <label>–î–∞–Ω—Å–Ω—ã –Ω—ç—Ä</label>
          <input
            type="text"
            value={dansniiNer}
            placeholder="–î–∞–Ω—Å–Ω—ã –Ω—ç—Ä—ç—ç –æ—Ä—É—É–ª–Ω–∞ —É—É"
            onChange={(e) => setDansniiNer(e.target.value)}
            style={styles.input}
          />
        </div>

        <div style={styles.row}>
          <label>–î–∞–Ω—Å–Ω—ã –¥—É–≥–∞–∞—Ä</label>
          <input
            type="text"
            value={dansniiDugaar}
            readOnly
            style={{ ...styles.input, background: "#f9f9f9" }}
          />
        </div>

        <div style={styles.row}>
          <label>–í–∞–ª—é—Ç</label>
          <input
            type="text"
            value={currency}
            readOnly
            style={{ ...styles.input, background: "#f9f9f9" }}
          />
        </div>

        <div style={styles.buttonRow}>
          <button style={styles.submitButton} onClick={handleCreate}>
            –î–∞–Ω—Å “Ø“Ø—Å–≥—ç—Ö
          </button>
          <button style={styles.cancelButton} onClick={handleClose}>
            –ë–æ–ª–∏—Ö
          </button>
        </div>
      </div>
    </div>
  );
};

const styles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    width: "100vw",
    height: "100vh",
    background: "rgba(0, 0, 0, 0.3)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    zIndex: 1000,
  },
  modal: {
    width: "600px",
    maxHeight: "90vh",
    overflowY: "auto",
    background: "#fff",
    padding: "20px",
    borderRadius: "5px",
    boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
    display: "flex",
    flexDirection: "column",
  },
  title: {
    fontSize: "18px",
    marginBottom: "15px",
    borderBottom: "1px solid #ddd",
    paddingBottom: "10px",
  },
  row: {
    display: "flex",
    flexDirection: "column",
    marginBottom: "10px",
  },
  input: {
    padding: "8px",
    fontSize: "14px",
    border: "1px solid #ccc",
    borderRadius: "4px",
    width: "100%",
    boxSizing: "border-box",
  },
  buttonRow: {
    display: "flex",
    justifyContent: "flex-end",
    gap: "10px",
    marginTop: "15px",
  },
  submitButton: {
    background: "#7367f0",
    color: "#fff",
    border: "none",
    padding: "10px 15px",
    cursor: "pointer",
    borderRadius: "4px",
  },
  cancelButton: {
    background: "#ccc",
    border: "none",
    padding: "10px 15px",
    cursor: "pointer",
    borderRadius: "4px",
  },
};

export default CreateAccount;
